import React from 'react'
import './css/mine.css'
import {connect} from 'react-redux'
import {NavLink,Route} from 'react-router-dom'
import {getNowRouter} from '../redux/actions/actions.js'
import SignForm from './SignForm.js'
import PromptBox from './PromptBox.js'
import HeadPortrait from './HeadPortrait.js'
class Mine extends React.Component{
  state={
    PromptBox:false
  }
  componentDidMount(){
    this.props.dispatch(getNowRouter(''))
    if(!this.props.user.username){
      this.props.history.push('/mine/signin')
    }
  
  }
  signOut(){
    sessionStorage.userId=''
    this.props.dispatch({type:"UNSIGN"})
    this.setState({PromptBox:false})
  }
  PromptBoxNone(){
    this.setState({PromptBox:false})
  }
  render(){
    return (
      <div className='mine'>
          {
            this.props.user.username ?//
            <div>
              <HeadPortrait username={this.props.user.username}/>
              <ul className="mine_ul">
                <li style={{textAlign:'center'}}>{this.props.user.username}</li>
                <li><span className='iconfont_mine_ul'>&#xe654;</span><span className='right'>&#xe662;</span></li>
                <li><span className='iconfont_mine_ul'>&#xe6c0;</span><span className='right'>&#xe662;</span></li>
                <li><span className='iconfont_mine_ul'>&#xe630;</span><span className='right'>&#xe662;</span></li>
                <li><span className='iconfont_mine_ul'>&#xe61e;</span><span className='right'>&#xe662;</span></li>
                <li><span className='iconfont_mine_ul'>&#xe60c;</span>:15133555096<span className='right'>&#xe662;</span></li>
              </ul>

              <button className='unsign' onClick={()=>{this.setState({PromptBox:true})}} ></button>
            </div>
            :
            this.props.user.userId?   //
            <div>....</div>
            :                             //
            <div className='mine_sign_form'>
              <div className='mine_sign_switch'>
                <NavLink activeClassName='navsign' to='/mine/signin'></NavLink>
                <NavLink activeClassName='navsign' to='/mine/signup'></NavLink>
              </div>
              <Route path='/mine/:id' component={SignForm}/>
            </div>
          }
          {this.state.PromptBox?
            <PromptBox title=',?' cancel={this.PromptBoxNone.bind(this)} sure={this.signOut.bind(this)}/>
          :null
          }
      </div>
    )
  }
}
const mapStateToProps=store=>({
  user:store.user,

})
export default connect(mapStateToProps)(Mine)



